import React from 'react';
import s from './Navbar.module.css';
import {NavLink} from "react-router-dom";


const Navbar = () => {
    return (
        <div className={s.nav}>
        <nav>

                <div className={`${s.item} ${s.active}`}><NavLink to="/profile"
                                                                  activeClassName={s.active}>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    Profile
                </NavLink></div>
                <div className={s.item}><NavLink to="/dialogs" activeClassName={s.active}>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    Messages
                </NavLink></div>
                <div className={s.item}><NavLink to="/users" activeClassName={s.active}>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    Users
                </NavLink></div>
                <div className={s.item}><NavLink to={"/news"} activeClassName={s.active}>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    News
                </NavLink></div>
                <div className={s.item}><NavLink to={"/music"} activeClassName={s.active}>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    Music
                </NavLink></div>
                <div className={s.item}><NavLink to={"/settings"} activeClassName={s.active}>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    Settings
                </NavLink></div>
        </nav>
                <div className={s.additionalBlock}>Best Friends
                    <div className={s.additionalBlockImg}><img src="https://data.whicdn.com/images/67919136/original.jpg" alt=""/></div>
                </div>
        </div>
    )
}

export default Navbar;